'use client';

import { useState, useEffect } from 'react';
import styles from './admin.module.css';

interface User {
    id: string;
    discordId: string;
    username: string;
    email: string | null;
    avatar: string | null;
    isAuthorized: boolean;
    authExpiry: string | null;
    isAdmin: boolean;
    createdAt: string;
}

interface RobloxAccount {
    id: string;
    user_id: string;
    account_name: string;
    roblox_user_id: string;
    cookie: string;
    is_active: boolean;
    last_used: string | null;
    created_at: string;
}

interface UserWithAccounts extends User {
    roblox_accounts: RobloxAccount[];
}

interface Session {
    id: string;
    userId: string;
    ip: string | null;
    userAgent: string | null;
    createdAt: string;
    user: { username: string; discordId: string };
}

interface BlacklistEntry {
    id: string;
    username: string;
    robloxUserId: string | null;
    reason: string | null;
    addedBy: string;
    createdAt: string;
}

interface Analytics {
    stats: {
        totalUsers: number;
        totalAccounts: number;
        activeSessions: number;
        totalLogs: number;
    };
    recentActivity: {
        accepted: number;
        declined: number;
        blocked: number;
    };
    recentLogsCount: number;
}

interface FilterLog {
    id: string;
    action: string;
    roblox_username: string;
    roblox_user_id: string;
    reason: string | null;
    created_at: string;
    roblox_accounts: { account_name: string } | null;
}

type Tab = 'analytics' | 'logs' | 'users' | 'accounts' | 'sessions' | 'blacklist' | 'settings';

export default function AdminDashboard() {
    const [tab, setTab] = useState<Tab>('analytics');
    const [users, setUsers] = useState<User[]>([]);
    const [usersWithAccounts, setUsersWithAccounts] = useState<UserWithAccounts[]>([]);
    const [sessions, setSessions] = useState<Session[]>([]);
    const [blacklist, setBlacklist] = useState<BlacklistEntry[]>([]);
    const [totalAccounts, setTotalAccounts] = useState(0);
    const [loading, setLoading] = useState(true);

    // Analytics state
    const [analytics, setAnalytics] = useState<Analytics | null>(null);

    // Logs state
    const [logs, setLogs] = useState<FilterLog[]>([]);
    const [logsPage, setLogsPage] = useState(1);
    const [logsTotalPages, setLogsTotalPages] = useState(1);

    // Settings state
    const [proxyList, setProxyList] = useState('');
    const [proxyCount, setProxyCount] = useState(0);
    const [savingSettings, setSavingSettings] = useState(false);

    // Avatar cache
    const [avatars, setAvatars] = useState<Record<string, string>>({});

    // Modal states
    const [showAuthModal, setShowAuthModal] = useState(false);
    const [showBlacklistModal, setShowBlacklistModal] = useState(false);
    const [showAccountModal, setShowAccountModal] = useState(false);
    const [selectedUser, setSelectedUser] = useState<User | null>(null);
    const [selectedAccount, setSelectedAccount] = useState<RobloxAccount | null>(null);
    const [authDuration, setAuthDuration] = useState('30');
    const [newBlacklistUsername, setNewBlacklistUsername] = useState('');
    const [newBlacklistReason, setNewBlacklistReason] = useState('');

    useEffect(() => {
        fetchData();
    }, [tab]);

    // Refetch logs when page changes
    useEffect(() => {
        if (tab === 'logs') {
            fetchData();
        }
    }, [logsPage]);

    // Fetch avatars when accounts tab is loaded
    useEffect(() => {
        if (tab === 'accounts' && usersWithAccounts.length > 0) {
            const allUserIds = usersWithAccounts.flatMap(u =>
                u.roblox_accounts.map(a => a.roblox_user_id)
            );
            if (allUserIds.length > 0) {
                fetchAvatars(allUserIds);
            }
        }
    }, [tab, usersWithAccounts]);

    const fetchAvatars = async (userIds: string[]) => {
        try {
            const res = await fetch(`https://thumbnails.roproxy.com/v1/users/avatar-headshot?userIds=${userIds.join(',')}&size=48x48&format=Png&isCircular=false`);
            const data = await res.json();
            const avatarMap: Record<string, string> = {};
            data.data?.forEach((item: { targetId: number; imageUrl: string }) => {
                avatarMap[String(item.targetId)] = item.imageUrl;
            });
            setAvatars(prev => ({ ...prev, ...avatarMap }));
        } catch (error) {
            console.error('Error fetching avatars:', error);
        }
    };

    const fetchData = async () => {
        setLoading(true);
        try {
            if (tab === 'analytics') {
                const res = await fetch('/api/admin/analytics');
                const data = await res.json();
                setAnalytics(data);
            } else if (tab === 'logs') {
                const res = await fetch(`/api/admin/logs?page=${logsPage}`);
                const data = await res.json();
                setLogs(data.logs || []);
                setLogsTotalPages(data.pagination?.totalPages || 1);
            } else if (tab === 'users') {
                const res = await fetch('/api/admin/users');
                const data = await res.json();
                setUsers(data.users || []);
            } else if (tab === 'accounts') {
                const res = await fetch('/api/admin/accounts');
                const data = await res.json();
                setUsersWithAccounts(data.users || []);
                setTotalAccounts(data.totalAccounts || 0);
            } else if (tab === 'sessions') {
                const res = await fetch('/api/admin/sessions');
                const data = await res.json();
                setSessions(data.sessions || []);
            } else if (tab === 'blacklist') {
                const res = await fetch('/api/admin/blacklist');
                const data = await res.json();
                setBlacklist(data.blacklist || []);
            } else if (tab === 'settings') {
                const res = await fetch('/api/admin/settings');
                const data = await res.json();
                setProxyList(data.proxies || '');
                setProxyCount(data.proxyCount || 0);
            }
        } catch (error) {
            console.error('Error fetching data:', error);
        } finally {
            setLoading(false);
        }
    };

    const authorizeUser = async () => {
        if (!selectedUser) return;

        try {
            const res = await fetch('/api/admin/users', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({
                    userId: selectedUser.id,
                    days: parseInt(authDuration),
                }),
            });

            if (res.ok) {
                setShowAuthModal(false);
                setSelectedUser(null);
                fetchData();
            }
        } catch (error) {
            console.error('Error authorizing user:', error);
        }
    };

    const revokeUser = async (userId: string) => {
        if (!confirm('Are you sure you want to revoke this user\'s access?')) return;

        try {
            const res = await fetch('/api/admin/users', {
                method: 'DELETE',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ userId }),
            });

            if (res.ok) {
                fetchData();
            }
        } catch (error) {
            console.error('Error revoking user:', error);
        }
    };

    const deleteAccount = async (accountId: string) => {
        if (!confirm('Delete this Roblox account?')) return;

        try {
            const res = await fetch('/api/admin/accounts', {
                method: 'DELETE',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ accountId }),
            });

            if (res.ok) {
                setShowAccountModal(false);
                setSelectedAccount(null);
                fetchData();
            }
        } catch (error) {
            console.error('Error deleting account:', error);
        }
    };

    const addToBlacklist = async () => {
        if (!newBlacklistUsername.trim()) return;

        try {
            const res = await fetch('/api/admin/blacklist', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({
                    input: newBlacklistUsername.trim(),
                    reason: newBlacklistReason.trim() || null,
                }),
            });

            if (res.ok) {
                setShowBlacklistModal(false);
                setNewBlacklistUsername('');
                setNewBlacklistReason('');
                fetchData();
            } else {
                const data = await res.json();
                alert(data.error || 'Failed to add to blacklist');
            }
        } catch (error) {
            console.error('Error adding to blacklist:', error);
        }
    };

    const removeFromBlacklist = async (username: string) => {
        if (!confirm(`Remove "${username}" from blacklist?`)) return;

        try {
            const res = await fetch('/api/admin/blacklist', {
                method: 'DELETE',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ username }),
            });

            if (res.ok) {
                fetchData();
            }
        } catch (error) {
            console.error('Error removing from blacklist:', error);
        }
    };

    const saveSettings = async () => {
        setSavingSettings(true);
        try {
            const res = await fetch('/api/admin/settings', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ proxies: proxyList }),
            });
            const data = await res.json();
            if (res.ok) {
                setProxyCount(data.proxyCount || 0);
                alert('Settings saved successfully!');
            }
        } catch (error) {
            console.error('Error saving settings:', error);
            alert('Failed to save settings');
        } finally {
            setSavingSettings(false);
        }
    };

    const handleLogout = async () => {
        await fetch('/api/auth/logout', { method: 'POST' });
        window.location.href = '/';
    };

    const formatDate = (dateString: string | null) => {
        if (!dateString) return '-';
        return new Date(dateString).toLocaleDateString('en-US', {
            month: 'short',
            day: 'numeric',
            year: 'numeric',
            hour: '2-digit',
            minute: '2-digit',
        });
    };

    const copyToClipboard = (text: string) => {
        navigator.clipboard.writeText(text);
        alert('Copied to clipboard!');
    };

    const openAccountDetails = (account: RobloxAccount) => {
        setSelectedAccount(account);
        setShowAccountModal(true);
    };

    return (
        <div className={styles.container}>
            <nav className={styles.nav}>
                <div className={styles.navBrand}>AntiBot Admin</div>
                <div className={styles.navRight}>
                    <a href="/dashboard" className={styles.dashLink}>Dashboard</a>
                    <button onClick={handleLogout} className={styles.logoutBtn}>Logout</button>
                </div>
            </nav>

            <main className={styles.main}>
                <div className={styles.header}>
                    <h1>Admin Dashboard</h1>
                    <p>Manage users, accounts, sessions, and blacklist</p>
                </div>

                <div className={styles.tabs}>
                    <button
                        className={`${styles.tab} ${tab === 'analytics' ? styles.active : ''}`}
                        onClick={() => setTab('analytics')}
                    >
                        Analytics
                    </button>
                    <button
                        className={`${styles.tab} ${tab === 'logs' ? styles.active : ''}`}
                        onClick={() => setTab('logs')}
                    >
                        Logs
                    </button>
                    <button
                        className={`${styles.tab} ${tab === 'users' ? styles.active : ''}`}
                        onClick={() => setTab('users')}
                    >
                        Users
                    </button>
                    <button
                        className={`${styles.tab} ${tab === 'accounts' ? styles.active : ''}`}
                        onClick={() => setTab('accounts')}
                    >
                        Accounts
                    </button>
                    <button
                        className={`${styles.tab} ${tab === 'sessions' ? styles.active : ''}`}
                        onClick={() => setTab('sessions')}
                    >
                        Sessions
                    </button>
                    <button
                        className={`${styles.tab} ${tab === 'blacklist' ? styles.active : ''}`}
                        onClick={() => setTab('blacklist')}
                    >
                        Blacklist
                    </button>
                    <button
                        className={`${styles.tab} ${tab === 'settings' ? styles.active : ''}`}
                        onClick={() => setTab('settings')}
                    >
                        Settings
                    </button>
                </div>

                {loading ? (
                    <div className={styles.loading}>
                        <div className={styles.spinner} />
                    </div>
                ) : (
                    <>
                        {/* Analytics Tab */}
                        {tab === 'analytics' && analytics && (
                            <div className={styles.card}>
                                <div className={styles.cardHeader}>
                                    <h2>System Analytics</h2>
                                </div>

                                <div style={{
                                    display: 'grid',
                                    gridTemplateColumns: 'repeat(auto-fit, minmax(200px, 1fr))',
                                    gap: '16px',
                                    padding: '20px'
                                }}>
                                    <div style={{
                                        background: 'linear-gradient(135deg, #3b82f6 0%, #2563eb 100%)',
                                        padding: '20px',
                                        borderRadius: '12px',
                                        color: 'white'
                                    }}>
                                        <div style={{ fontSize: '32px', fontWeight: 700 }}>{analytics?.stats?.totalUsers ?? 0}</div>
                                        <div style={{ opacity: 0.9, fontSize: '14px' }}>Total Users</div>
                                    </div>
                                    <div style={{
                                        background: 'linear-gradient(135deg, #10b981 0%, #059669 100%)',
                                        padding: '20px',
                                        borderRadius: '12px',
                                        color: 'white'
                                    }}>
                                        <div style={{ fontSize: '32px', fontWeight: 700 }}>{analytics?.stats?.totalAccounts ?? 0}</div>
                                        <div style={{ opacity: 0.9, fontSize: '14px' }}>Roblox Accounts</div>
                                    </div>
                                    <div style={{
                                        background: 'linear-gradient(135deg, #8b5cf6 0%, #7c3aed 100%)',
                                        padding: '20px',
                                        borderRadius: '12px',
                                        color: 'white'
                                    }}>
                                        <div style={{ fontSize: '32px', fontWeight: 700 }}>{analytics?.stats?.activeSessions ?? 0}</div>
                                        <div style={{ opacity: 0.9, fontSize: '14px' }}>Active Sessions</div>
                                    </div>
                                    <div style={{
                                        background: 'linear-gradient(135deg, #f59e0b 0%, #d97706 100%)',
                                        padding: '20px',
                                        borderRadius: '12px',
                                        color: 'white'
                                    }}>
                                        <div style={{ fontSize: '32px', fontWeight: 700 }}>{analytics?.stats?.totalLogs ?? 0}</div>
                                        <div style={{ opacity: 0.9, fontSize: '14px' }}>Total Actions</div>
                                    </div>
                                </div>

                                <div style={{ padding: '20px', borderTop: '1px solid rgba(255,255,255,0.1)' }}>
                                    <h3 style={{ marginBottom: '16px' }}>Last 7 Days Activity</h3>
                                    <div style={{ display: 'flex', gap: '24px', flexWrap: 'wrap' }}>
                                        <div style={{ display: 'flex', alignItems: 'center', gap: '8px' }}>
                                            <span style={{
                                                width: '12px', height: '12px', borderRadius: '50%',
                                                background: '#10b981'
                                            }}></span>
                                            <span>Accepted: <strong>{analytics?.recentActivity?.accepted ?? 0}</strong></span>
                                        </div>
                                        <div style={{ display: 'flex', alignItems: 'center', gap: '8px' }}>
                                            <span style={{
                                                width: '12px', height: '12px', borderRadius: '50%',
                                                background: '#f59e0b'
                                            }}></span>
                                            <span>Declined: <strong>{analytics?.recentActivity?.declined ?? 0}</strong></span>
                                        </div>
                                        <div style={{ display: 'flex', alignItems: 'center', gap: '8px' }}>
                                            <span style={{
                                                width: '12px', height: '12px', borderRadius: '50%',
                                                background: '#ef4444'
                                            }}></span>
                                            <span>Blocked: <strong>{analytics?.recentActivity?.blocked ?? 0}</strong></span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        )}

                        {/* Logs Tab */}
                        {tab === 'logs' && (
                            <div className={styles.card}>
                                <div className={styles.cardHeader}>
                                    <h2>Filter Logs</h2>
                                    <span className={styles.count}>{logs.length} entries (page {logsPage}/{logsTotalPages})</span>
                                </div>
                                {logs.length === 0 ? (
                                    <p className={styles.emptyState}>No filter logs yet.</p>
                                ) : (
                                    <>
                                        <div className={styles.tableWrapper}>
                                            <table className={styles.table}>
                                                <thead>
                                                    <tr>
                                                        <th>Action</th>
                                                        <th>Username</th>
                                                        <th>User ID</th>
                                                        <th>Account</th>
                                                        <th>Reason</th>
                                                        <th>Date</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    {logs.map(log => (
                                                        <tr key={log.id}>
                                                            <td>
                                                                <span style={{
                                                                    padding: '4px 8px',
                                                                    borderRadius: '4px',
                                                                    fontSize: '12px',
                                                                    fontWeight: 500,
                                                                    background: log.action === 'accepted' ? 'rgba(16, 185, 129, 0.2)' :
                                                                        log.action === 'declined' ? 'rgba(245, 158, 11, 0.2)' :
                                                                            'rgba(239, 68, 68, 0.2)',
                                                                    color: log.action === 'accepted' ? '#10b981' :
                                                                        log.action === 'declined' ? '#f59e0b' : '#ef4444'
                                                                }}>
                                                                    {log.action}
                                                                </span>
                                                            </td>
                                                            <td>{log.roblox_username}</td>
                                                            <td className={styles.mono}>{log.roblox_user_id}</td>
                                                            <td>{log.roblox_accounts?.account_name || '-'}</td>
                                                            <td style={{ maxWidth: '200px', overflow: 'hidden', textOverflow: 'ellipsis' }}>
                                                                {log.reason || '-'}
                                                            </td>
                                                            <td>{new Date(log.created_at).toLocaleString()}</td>
                                                        </tr>
                                                    ))}
                                                </tbody>
                                            </table>
                                        </div>
                                        <div style={{
                                            padding: '16px',
                                            display: 'flex',
                                            justifyContent: 'center',
                                            gap: '8px'
                                        }}>
                                            <button
                                                className={styles.btnSecondary}
                                                disabled={logsPage <= 1}
                                                onClick={() => { setLogsPage(p => p - 1); }}
                                            >
                                                Previous
                                            </button>
                                            <span style={{ padding: '8px 16px' }}>
                                                Page {logsPage} of {logsTotalPages}
                                            </span>
                                            <button
                                                className={styles.btnSecondary}
                                                disabled={logsPage >= logsTotalPages}
                                                onClick={() => { setLogsPage(p => p + 1); }}
                                            >
                                                Next
                                            </button>
                                        </div>
                                    </>
                                )}
                            </div>
                        )}

                        {/* Users Tab */}
                        {tab === 'users' && (
                            <div className={styles.card}>
                                <div className={styles.cardHeader}>
                                    <h2>Registered Users</h2>
                                    <span className={styles.count}>{users.length} users</span>
                                </div>
                                <div className={styles.tableWrapper}>
                                    <table className={styles.table}>
                                        <thead>
                                            <tr>
                                                <th>User</th>
                                                <th>Discord ID</th>
                                                <th>Email</th>
                                                <th>Status</th>
                                                <th>Expires</th>
                                                <th>Actions</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {users.map((user) => (
                                                <tr key={user.id}>
                                                    <td>
                                                        <div className={styles.userCell}>
                                                            {user.avatar && (
                                                                <img
                                                                    src={`https://cdn.discordapp.com/avatars/${user.discordId}/${user.avatar}.png`}
                                                                    alt={user.username}
                                                                    className={styles.avatar}
                                                                />
                                                            )}
                                                            <span>{user.username}</span>
                                                            {user.isAdmin && <span className={styles.adminBadge}>Admin</span>}
                                                        </div>
                                                    </td>
                                                    <td className={styles.mono}>{user.discordId}</td>
                                                    <td>{user.email || '-'}</td>
                                                    <td>
                                                        <span className={user.isAuthorized ? styles.badgeSuccess : styles.badgeDanger}>
                                                            {user.isAuthorized ? 'Authorized' : 'Not Authorized'}
                                                        </span>
                                                    </td>
                                                    <td>
                                                        {user.authExpiry
                                                            ? new Date(user.authExpiry).toLocaleDateString()
                                                            : user.isAuthorized ? 'Never' : '-'
                                                        }
                                                    </td>
                                                    <td>
                                                        <div className={styles.actions}>
                                                            {!user.isAdmin && (
                                                                <>
                                                                    <button
                                                                        className={styles.btnSmSuccess}
                                                                        onClick={() => {
                                                                            setSelectedUser(user);
                                                                            setShowAuthModal(true);
                                                                        }}
                                                                    >
                                                                        Authorize
                                                                    </button>
                                                                    {user.isAuthorized && (
                                                                        <button
                                                                            className={styles.btnSmDanger}
                                                                            onClick={() => revokeUser(user.id)}
                                                                        >
                                                                            Revoke
                                                                        </button>
                                                                    )}
                                                                </>
                                                            )}
                                                        </div>
                                                    </td>
                                                </tr>
                                            ))}
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        )}

                        {/* Accounts Tab */}
                        {tab === 'accounts' && (
                            <div className={styles.card}>
                                <div className={styles.cardHeader}>
                                    <h2>Roblox Accounts</h2>
                                    <span className={styles.count}>{totalAccounts} total accounts</span>
                                </div>

                                {usersWithAccounts.length === 0 ? (
                                    <p className={styles.emptyState}>No users with Roblox accounts</p>
                                ) : (
                                    <div className={styles.accountsGrid}>
                                        {usersWithAccounts.map(user => (
                                            <div key={user.id} className={styles.userAccountCard}>
                                                <div className={styles.userAccountHeader}>
                                                    <div className={styles.userCell}>
                                                        {user.avatar && (
                                                            <img
                                                                src={`https://cdn.discordapp.com/avatars/${user.discordId}/${user.avatar}.png`}
                                                                alt={user.username}
                                                                className={styles.avatar}
                                                            />
                                                        )}
                                                        <div>
                                                            <span className={styles.userAccountName}>{user.username}</span>
                                                            <span className={styles.userAccountMeta}>
                                                                {user.roblox_accounts.length} account{user.roblox_accounts.length !== 1 ? 's' : ''}
                                                            </span>
                                                        </div>
                                                    </div>
                                                </div>

                                                {user.roblox_accounts.length > 0 ? (
                                                    <div className={styles.accountsList}>
                                                        {user.roblox_accounts.map(account => (
                                                            <div
                                                                key={account.id}
                                                                className={styles.accountItem}
                                                                onClick={() => openAccountDetails(account)}
                                                                style={{ cursor: 'pointer' }}
                                                            >
                                                                <img
                                                                    src={avatars[account.roblox_user_id] || '/placeholder-avatar.png'}
                                                                    alt=""
                                                                    className={styles.robloxAvatar}
                                                                    onError={(e) => {
                                                                        (e.target as HTMLImageElement).src = '/placeholder-avatar.png';
                                                                    }}
                                                                />
                                                                <div className={styles.accountDetails}>
                                                                    <span className={styles.accountLink}>
                                                                        {account.account_name}
                                                                    </span>
                                                                    <span className={styles.accountMeta}>
                                                                        {account.last_used
                                                                            ? `Last used: ${formatDate(account.last_used)}`
                                                                            : 'Never used'}
                                                                    </span>
                                                                </div>
                                                                <span className={styles.viewButton}>View</span>
                                                            </div>
                                                        ))}
                                                    </div>
                                                ) : (
                                                    <p className={styles.noAccounts}>No Roblox accounts</p>
                                                )}
                                            </div>
                                        ))}
                                    </div>
                                )}
                            </div>
                        )}

                        {/* Sessions Tab */}
                        {tab === 'sessions' && (
                            <div className={styles.card}>
                                <div className={styles.cardHeader}>
                                    <h2>Active Sessions</h2>
                                    <span className={styles.count}>{sessions.length} sessions</span>
                                </div>
                                <div className={styles.tableWrapper}>
                                    <table className={styles.table}>
                                        <thead>
                                            <tr>
                                                <th>User</th>
                                                <th>IP Address</th>
                                                <th>User Agent</th>
                                                <th>Created</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {sessions.map((session) => (
                                                <tr key={session.id}>
                                                    <td>{session.user.username}</td>
                                                    <td className={styles.mono}>{session.ip || 'Unknown'}</td>
                                                    <td className={styles.truncate}>{session.userAgent || 'Unknown'}</td>
                                                    <td>{new Date(session.createdAt).toLocaleString()}</td>
                                                </tr>
                                            ))}
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        )}

                        {/* Blacklist Tab */}
                        {tab === 'blacklist' && (
                            <div className={styles.card}>
                                <div className={styles.cardHeader}>
                                    <h2>Roblox Blacklist</h2>
                                    <button
                                        className={styles.btnPrimary}
                                        onClick={() => setShowBlacklistModal(true)}
                                    >
                                        + Add User
                                    </button>
                                </div>
                                {blacklist.length === 0 ? (
                                    <p className={styles.emptyState}>No blacklisted users yet.</p>
                                ) : (
                                    <div className={styles.tableWrapper}>
                                        <table className={styles.table}>
                                            <thead>
                                                <tr>
                                                    <th>Username</th>
                                                    <th>User ID</th>
                                                    <th>Reason</th>
                                                    <th>Added By</th>
                                                    <th>Date Added</th>
                                                    <th>Actions</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {blacklist.map((entry) => (
                                                    <tr key={entry.id}>
                                                        <td className={styles.mono}>{entry.username}</td>
                                                        <td>
                                                            {entry.robloxUserId ? (
                                                                <a
                                                                    href={`https://www.roblox.com/users/${entry.robloxUserId}/profile`}
                                                                    target="_blank"
                                                                    rel="noopener noreferrer"
                                                                    className={styles.mono}
                                                                    style={{ color: '#5865f2', textDecoration: 'none' }}
                                                                >
                                                                    {entry.robloxUserId}
                                                                </a>
                                                            ) : (
                                                                <span style={{ opacity: 0.5 }}>-</span>
                                                            )}
                                                        </td>
                                                        <td>{entry.reason || '-'}</td>
                                                        <td>{entry.addedBy}</td>
                                                        <td>{new Date(entry.createdAt).toLocaleDateString()}</td>
                                                        <td>
                                                            <button
                                                                className={styles.btnSmDanger}
                                                                onClick={() => removeFromBlacklist(entry.username)}
                                                            >
                                                                Remove
                                                            </button>
                                                        </td>
                                                    </tr>
                                                ))}
                                            </tbody>
                                        </table>
                                    </div>
                                )}
                            </div>
                        )}

                        {/* Settings Tab */}
                        {tab === 'settings' && (
                            <div className={styles.card}>
                                <div className={styles.cardHeader}>
                                    <h2>Global Settings</h2>
                                </div>

                                <div className={styles.formGroup} style={{ marginTop: '16px' }}>
                                    <label style={{ marginBottom: '8px', display: 'block' }}>
                                        Proxy List ({proxyCount} proxies configured)
                                    </label>
                                    <p style={{ fontSize: '13px', opacity: 0.7, marginBottom: '12px' }}>
                                        Add proxies for API requests. One proxy per line.
                                        Format: http://user:pass@host:port or http://host:port
                                    </p>
                                    <textarea
                                        value={proxyList}
                                        onChange={(e) => setProxyList(e.target.value)}
                                        placeholder={'http://user:pass@proxy1.example.com:8080\nhttp://user:pass@proxy2.example.com:8080'}
                                        className={styles.input}
                                        style={{
                                            minHeight: '200px',
                                            fontFamily: 'monospace',
                                            fontSize: '13px',
                                            resize: 'vertical',
                                        }}
                                    />
                                </div>

                                <div style={{ marginTop: '16px' }}>
                                    <button
                                        className={styles.btnPrimary}
                                        onClick={saveSettings}
                                        disabled={savingSettings}
                                    >
                                        {savingSettings ? 'Saving...' : 'Save Settings'}
                                    </button>
                                </div>
                            </div>
                        )}
                    </>
                )}
            </main>

            {/* Authorization Modal */}
            {showAuthModal && selectedUser && (
                <div className={styles.modalOverlay} onClick={() => setShowAuthModal(false)}>
                    <div className={styles.modal} onClick={(e) => e.stopPropagation()}>
                        <h3>Authorize User</h3>
                        <p>Set authorization duration for <strong>{selectedUser.username}</strong></p>
                        <div className={styles.formGroup}>
                            <label>Duration (days)</label>
                            <select
                                value={authDuration}
                                onChange={(e) => setAuthDuration(e.target.value)}
                                className={styles.select}
                            >
                                <option value="7">7 days</option>
                                <option value="14">14 days</option>
                                <option value="30">30 days</option>
                                <option value="90">90 days</option>
                                <option value="365">1 year</option>
                                <option value="0">Permanent</option>
                            </select>
                        </div>
                        <div className={styles.modalActions}>
                            <button className={styles.btnSecondary} onClick={() => setShowAuthModal(false)}>
                                Cancel
                            </button>
                            <button className={styles.btnSuccess} onClick={authorizeUser}>
                                Authorize
                            </button>
                        </div>
                    </div>
                </div>
            )}

            {/* Account Details Modal */}
            {showAccountModal && selectedAccount && (
                <div className={styles.modalOverlay} onClick={() => setShowAccountModal(false)}>
                    <div className={styles.modal} onClick={(e) => e.stopPropagation()}>
                        <div className={styles.accountModalHeader}>
                            <img
                                src={avatars[selectedAccount.roblox_user_id] || '/placeholder-avatar.png'}
                                alt=""
                                className={styles.accountModalAvatar}
                            />
                            <div>
                                <h3>{selectedAccount.account_name}</h3>
                                <a
                                    href={`https://www.roblox.com/users/${selectedAccount.roblox_user_id}/profile`}
                                    target="_blank"
                                    rel="noopener noreferrer"
                                    className={styles.profileLink}
                                >
                                    View Roblox Profile
                                </a>
                            </div>
                        </div>

                        <div className={styles.accountDetailsList}>
                            <div className={styles.detailRow}>
                                <span className={styles.detailLabel}>User ID</span>
                                <span className={styles.detailValue}>{selectedAccount.roblox_user_id}</span>
                            </div>
                            <div className={styles.detailRow}>
                                <span className={styles.detailLabel}>Last Used</span>
                                <span className={styles.detailValue}>{formatDate(selectedAccount.last_used)}</span>
                            </div>
                            <div className={styles.detailRow}>
                                <span className={styles.detailLabel}>Added</span>
                                <span className={styles.detailValue}>{formatDate(selectedAccount.created_at)}</span>
                            </div>
                            <div className={styles.detailRow}>
                                <span className={styles.detailLabel}>Status</span>
                                <span className={selectedAccount.is_active ? styles.badgeSuccess : styles.badgeDanger}>
                                    {selectedAccount.is_active ? 'Active' : 'Inactive'}
                                </span>
                            </div>
                        </div>

                        <div className={styles.cookieSection}>
                            <label className={styles.detailLabel}>Cookie</label>
                            <div className={styles.cookieBox}>
                                <code>{selectedAccount.cookie.slice(0, 50)}...{selectedAccount.cookie.slice(-20)}</code>
                            </div>
                            <button
                                className={styles.btnCopy}
                                onClick={() => copyToClipboard(selectedAccount.cookie)}
                            >
                                Copy Full Cookie
                            </button>
                        </div>

                        <div className={styles.modalActions}>
                            <button className={styles.btnSecondary} onClick={() => setShowAccountModal(false)}>
                                Close
                            </button>
                            <button
                                className={styles.btnDanger}
                                onClick={() => deleteAccount(selectedAccount.id)}
                            >
                                Delete Account
                            </button>
                        </div>
                    </div>
                </div>
            )}

            {/* Blacklist Modal */}
            {showBlacklistModal && (
                <div className={styles.modalOverlay} onClick={() => setShowBlacklistModal(false)}>
                    <div className={styles.modal} onClick={(e) => e.stopPropagation()}>
                        <h3>Add to Blacklist</h3>
                        <div className={styles.formGroup}>
                            <label>Username or User ID</label>
                            <input
                                type="text"
                                value={newBlacklistUsername}
                                onChange={(e) => setNewBlacklistUsername(e.target.value)}
                                placeholder="Enter username or Roblox user ID"
                                className={styles.input}
                            />
                        </div>
                        <div className={styles.formGroup}>
                            <label>Reason (optional)</label>
                            <input
                                type="text"
                                value={newBlacklistReason}
                                onChange={(e) => setNewBlacklistReason(e.target.value)}
                                placeholder="Why are you blacklisting this user?"
                                className={styles.input}
                            />
                        </div>
                        <div className={styles.modalActions}>
                            <button className={styles.btnSecondary} onClick={() => setShowBlacklistModal(false)}>
                                Cancel
                            </button>
                            <button className={styles.btnDanger} onClick={addToBlacklist}>
                                Add to Blacklist
                            </button>
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
}
